<!--
 * @Descripttion: 打印报告列表
 * @Author: 曾元球
 * @Date: 2019-05-28 15:45:49
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-08 15:18:55
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center">
      <div class="head-title">打印报告</div>
      <div class="flex end">
        <div class="head-btn"
          v-if="btns.hasPrint"
          data-btn-id='104001001'
          @click="printPage">
          <img src="@/assets/img/d4.png"
            alt="打印">&nbsp;<span>打印</span>
        </div>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='登记日期：'>
          <el-date-picker type='daterange'
            v-model='dateRange'
            range-separator='至'
            start-placeholder='开始日期'
            end-placeholder='结束日期'
            :picker-options="{ disabledDate: time => time > new Date() }"
            value-format="yyyy-MM-dd"
            unlink-panels
            clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label='体检流水号：'>
          <el-input placeholder="请输入"
            v-model="querys.examUserId"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='姓名：'>
          <el-input placeholder="请输入"
            v-model="querys.userName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item label='状态：'>
          <el-select placeholder="请选择"
            v-model="querys.printStatus"
            clearable>
            <el-option value=''
              label="全部"></el-option>
            <el-option value='1'
              label="未打印"></el-option>
            <el-option value='2'
              label="已打印"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        ref="table"
        header-cell-class-name='custom-table-cell'
        row-key='sysGuid'
        @selection-change='handleSelect'
        @row-click='handleClickRow'
        :row-class-name='handleRowClass'
        stripe>
        <el-table-column type="selection"
          reserve-selection></el-table-column>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='体检流水号'
          prop='sysGuid'
          min-width='100'></el-table-column>
        <el-table-column label='姓名'
          prop='userName'></el-table-column>
        <el-table-column label='性别'
          prop='sexType'
          width='60'>
          <template slot-scope="scope">
            {{
              scope.row.sexType == 1 ? '男' :
              scope.row.sexType == 2 ? '女' : '不详'
            }}
          </template>
        </el-table-column>
        <el-table-column label='联系电话'
          prop='contactPhone'></el-table-column>
        <el-table-column label='开始体检时间'
          prop='createDate'>
          <template slot-scope="scope">
            {{scope.row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='完成体检时间'
          prop='updateDate'>
          <template slot-scope="scope">
            {{scope.row.updateDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='体检次数'
          prop='examNum'></el-table-column>
        <el-table-column label='打印人'
          prop='printMan'
          :formatter='({printMan}) => $options.filters.emptyData(printMan)'></el-table-column>
        <el-table-column label='打印时间'
          prop='printDate'>
          <template slot-scope="scope">
            {{scope.row.printDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='打印次数'
          prop='printNum'
          :formatter='({printNum}) => $options.filters.emptyData(printNum, 0)'>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination query-url='/user/examUser/showReportExamList'
        :query-obj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination'></bj-pagination>
    </div>

    <div ref='print-html-box'
      style="position:absolute;left:-8000px;top:0;visibility:hidden">
      <medical-report-html v-for="(item, i) in reportList"
        :key='i'
        :user='item.user'
        :projectList='item.smallProjectList'></medical-report-html>
    </div>
  </div>
</template>
<script>
import printMethod from '@/lib/print-method'
import MedicalReportHtml from '@/components/MedicalReportHtml'
const MAX_PRINT = 50
export default {
  name: 'PrintReport',
  components: { MedicalReportHtml },
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      dateRange: '',
      querys: {
        examUserId: '',
        createDate: '',
        startTime: '',
        endTime: '',
        userName: '',
        printStatus: '',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      reportList: []
    }
  },

  methods: {
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    // 监听选择框
    handleSelect(selection) {
      this.examIds = selection.map(item => item.sysGuid)
    },

    handleClickRow(row, column, e) {
      this.$refs.table.toggleRowSelection(row)
    },

    handleRowClass({ row }) {
      return !this.examIds ? '' : ~this.examIds.indexOf(row.sysGuid) ? 'is-selection' : ''
    },

    printPage() {
      if (!this.examIds || !this.examIds.length) {
        return this.$message.warning('请选择您要打印的体检报告')
      }

      if (this.examIds.length > MAX_PRINT) {
        return this.$message.warning(`一次最多打印${MAX_PRINT}份！`)
      }
      this.reportList = []
      let loading = null
      function inFn() {
        this.$post('/user/examUser/printRecord', {
          idList: this.examIds.toString()
        })
          .then(res => {
            this.$refs.pagination.initPageList()
            printMethod(this.$refs['print-html-box'], '体检报告')
            setTimeout(() => {
              loading.close()
            }, 800)
          })
          .catch(err => {
            this.$message.error('打印记录失败，无法打印！')
            loading.close()
          })
      }

      this.$post('/user/examUser/showReportExamBatch', {
        examUserIds: this.examIds.toString()
      }).then(({ data }) => {
        this.reportList = data

        // 点击确定前成功 loading 为 null 不会执行打印
        if (loading) this.$nextTick(() => inFn.call(this))
      })
      this.$confirm('确定打印视为打印一次！', {
        title: '温馨提示',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          loading = this.$loading({ text: '数据准备中...' })

          // 数据完成直接调用打印，没有就交出控制权
          if (this.reportList.length) this.$nextTick(() => inFn.call(this))
        })
        .catch(e => e)
    }
  },

  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasPrint: ~btnIds.indexOf('104001001') // 打印按钮权限
      }
    },
    orgList() {
      return this.$store.state.orgList
    }
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.querys.startTime = val[0]
        this.querys.endTime = `${val[1]} 23:59:59`
      } else {
        this.querys.startTime = ''
        this.querys.startTime = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    & + & {
      margin-left: 36px;
    }
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
  .end {
    margin-left: auto;
  }
}
.table-box {
  .el-table /deep/ tr {
    cursor: pointer;
  }
}
</style>
